.main-top {
    padding: 5.555555vw 8.333333vw 2.777777vw;
}

.main-top-text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main-top-text {
    font-family: 'GimhaeGaya';
    font-size: 40px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: -0.8px;
}

.main-top-3-container {
    margin-top: 32px;
}

.main-top-3-text {
    margin: 24px 0;
    
    font-size: 24px;
    font-weight: 500;
    text-align: center;
}

.main-top-3-list {
    display: flex;
    justify-content: space-evenly;
}

.main-bottom {
    padding: 40px 8.333333vw;
    
    background-color: rgba(0, 0, 0, 0.05);
}

.main-bottom-text {
    margin-bottom: 20px;

    font-size: 24px;
    font-weight: 500;
}

.main-bottom-container {
    margin-bottom: 40px;

    display: flex;
}

.main-bottom-board-list {
    flex: 8;
}

.main-bottom-popular-box {
    margin-left: 24px;
    
    flex: 4;
}   

.main-bottom-popular-card {
    padding: 24px;
    background-color: #ffffff;
}

.main-bottom-popular-text {
    margin-bottom: 24px;

    font-size: 24px;
    font-weight: 500;
    line-height: 100%;
}

.popular-chip {
    display: inline-flex;

    margin-right: 12px;
    margin-bottom: 12px;
    border: 1px solid rgba(0,0,0,0.5);
    border-radius: 30px;
    
    padding: 6px 20px;
    
    color: rgba(0,0,0,0.5);
    font-size: 14px;
    font-weight: 500;
    line-height: 100%;
}

.popular-chip:hover{
    cursor: pointer;
    background-color: rgba(0,0,0,0.15);
}

